/* SegmentedButton.css */
.segmented-button {
    display: flex;
    /* position: relative; */
    width: 234px;
    margin: 10px auto;
    position: relative;
    border-radius: 30px;
    background: #ebf9f5;
  }
  
  .button {
    position: relative;
    flex: 1;
    padding: 10px;
    text-align: center;
    text-decoration: none;
    color: #00b386;
    background-color: #ebf9f5;
    overflow: hidden;
    transition: background-color 0.3s ease;
    border-radius: 30px;
    margin: 0px auto;
  }
  
  .button.active {
    background-color: #00b386;
    color: #ebf9f5;
    z-index: 2;
  }
  
  .switch {
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background-color: #00b386;
    transition: transform 0.3s ease;
    border-radius: 30px;
  }
  
  .switch.right {
    transform: translateX(100%);
  }
  